import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../hooks/useGame';
import {
  Box,
  Button,
  List,
  ListItem,
  ListItemText,
  Typography,
  Paper,
  Divider,
  Snackbar,
  TextField,
  Stack,
  IconButton,
} from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import ReplayIcon from '@mui/icons-material/Replay';

const RoleCheck = () => {
  const { state, dispatch } = useGame();
  const navigate = useNavigate();
  const { players, topic, insiderCount } = state;
  const [showRoles, setShowRoles] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [customTopic, setCustomTopic] = useState('');

  const handleCopyToClipboard = () => {
    const insiders = players.filter(p => p.role === 'insider').map(p => p.name).join(', ');
    const textToCopy = `
: ${topic}
---
 (${insiderCount}): ${insiders}
---
:
${players.map(p => `- ${p.name}`).join('\n')}
    `;
    navigator.clipboard.writeText(textToCopy.trim());
    setSnackbarOpen(true);
  };

  const handleSetCustomTopic = () => {
    if (customTopic.trim() !== '') {
      dispatch({ type: 'SET_TOPIC', payload: customTopic.trim() });
    }
  };

  const handleGoToQuestionPhase = () => {
    dispatch({ type: 'CHANGE_PHASE', payload: 'QuestionPhase' });
    navigate('/question-phase');
  };
  
  const handleChangeTopicRandomly = () => {
    dispatch({ type: 'CHANGE_TOPIC_RANDOMLY' });
  };

  return (
    <Paper elevation={3} sx={{ p: 4 }}>
      <Typography variant="h5" component="h2" gutterBottom>
        
      </Typography>
      
      <Box sx={{ my: 3 }}>
        <Stack direction="row" spacing={1} sx={{ alignItems: 'center', mb: 2 }}>
          <Typography variant="h6"></Typography>
          <IconButton
            aria-label=""
            onClick={handleChangeTopicRandomly}
          >
            <ReplayIcon />
          </IconButton>
        </Stack>

        <Typography variant="h4" component="p" sx={{ fontWeight: 'bold', mb: 2 }}>
          {topic}
        </Typography>
        
        <Stack direction="row" spacing={1} sx={{ alignItems: 'center' }}>
          <TextField 
            label=""
            variant="outlined"
            size="small"
            value={customTopic}
            onChange={(e) => setCustomTopic(e.target.value)}
            sx={{ flexGrow: 1 }}
          />
          <Button 
            variant="contained" 
            onClick={handleSetCustomTopic}
            disabled={!customTopic.trim()}
            sx={{ fontWeight: 'bold' }}
          >
            
          </Button>
        </Stack>
      </Box>

      <Divider sx={{ my: 3 }} />

      <Typography variant="h6"></Typography>
      {!showRoles && (
        <Button variant="contained" onClick={() => setShowRoles(true)} sx={{ my: 2, fontWeight: 'bold' }}>
          
        </Button>
      )}
      
      {showRoles && (
        <List>
          {players.map((player) => (
            <ListItem key={player.name}>
              <ListItemText 
                primary={player.name}
                secondary={player.role === 'insider' ? '' : ''}
              />
            </ListItem>
          ))}
        </List>
      )}

      <Box sx={{ mt: 4, display: 'flex', flexDirection: 'column', gap: 2 }}>
        <Button
          variant="outlined"
          startIcon={<ContentCopyIcon />}
          onClick={handleCopyToClipboard}
          sx={{ fontWeight: 'bold' }}
        >
          
        </Button>
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={handleGoToQuestionPhase}
          disabled={!showRoles}
          sx={{ fontWeight: 'bold' }}
        >
          
        </Button>
        <Button
          variant="outlined"
          color="secondary"
          onClick={() => navigate('/')}
          sx={{ fontWeight: 'bold' }}
        >
          
        </Button>
      </Box>
      
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        message=""
      />
    </Paper>
  );
};

export default RoleCheck;
